import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import "assets/css/components/Header.scss"
import componentStyles from "assets/theme/components/admin-navbar.js";
import { useWallet } from '@solana/wallet-adapter-react';
import { WalletMultiButton } from '@solana/wallet-adapter-react-ui';
import SearchIcon from "@material-ui/icons/Search";
import settingPng from "assets/img/SolviewIcons/ChartPage/Setting.svg";
import solviewPng from "assets/img/brand/solview_1.png"
import { save_holder_data, save_gainer_data, save_chart_data, save_loser_data, save_address_data, save_otherAddress_data, save_promoted_data, save_transaction_data, save_symbol_data, save_pair_data, save_token_data, save_nft_data } from "redux/actions/provider";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import axios from "axios";
import config from "config/index.js"
require('@solana/wallet-adapter-react-ui/styles.css');

const useStyles = makeStyles(componentStyles);

export default function Header() {

  const history = useHistory();
  const dispatch = useDispatch();
  const { publicKey } = useWallet();
  const [solvPrice, setSolvPrice] = useState(0);
  const [searchData, setSearchData] = useState([])
  const [initData, setInitData] = useState([]);
  const [searchKey, setSearchKey] = useState("");

  ///==============function=======================///

  //to get wallet address that is connected
  const getWalletAddress = async () => {
    dispatch(save_address_data(publicKey.toBase58()));
  }

  //to get solana tokens list for search
  const getTokenList = async () => {
    let baseURL = 'https://api.solscan.io/tokens?offset=0&limit=10000&sortby=market_cap&sorttype=desc';
    let responseData = await axios.get(baseURL);
    if (responseData.data.data) {
      let allData = responseData.data.data.tokens;
      setInitData(allData)
    }
  }
  //to get current solview token price
  const getSolvPrice = async () => {
    let url = config.serverUrl + "/trade/trade";
    let tokenData = await axios.post(url, { token: 'SOLV' });
    let data = tokenData.data.data.items[0];
    if (data && data.price) {
      setSolvPrice(parseFloat(data.price));
    }
  }

  //to get transaction of pair address
  const getTrasaction = async (pairAddress) => {
    const res = await axios.get(`https://api.solscan.io/amm/txs?address=${pairAddress}&type=all&offset=0&limit=1`);
    if (res.data.data) {
      dispatch(save_transaction_data(res.data.data.items));
    }
  }

  //to get all pairs of token address
  const getPair = async (value) => {
    var res = await axios.get(`https://api.solscan.io/amm/pairs?source=raydium`);
    res = res.data.data.items;
    if (value != null) {
      var data = res;
      var dod = [];
      for (let i = 0; i < data.length; i++) {
        var name = data[i]['name'];
        if (name.toLowerCase().includes(value.toLowerCase()) || data[i]['address'] == value) {
          dod.push(data[i]);
        }
        else if (data[i]['base'] != null) {
          if (data[i]['base']['address'] == value || data[i]['quote']['address'] == value) {
            dod.push(data[i]);
          }
        }
      }
      res = dod;
      if (res) {
        dispatch(save_pair_data(res))
        if (res[0] && res[0].address) {
          getTrasaction(res[0].address)
        }
      }
    }
  }

  //to get biggest gainers
  const getGainer = async () => {
    let gainerData = await axios.get('https://api.solscan.io/tokens?offset=0&limit=10&sortby=price_change_24h&sorttype=desc');
    if (gainerData.data.data && gainerData.data.data.tokens) {
      dispatch(save_gainer_data(gainerData.data.data.tokens));
    }
  }

  //to get biggest losers
  const getLoser = async () => {
    let data = await axios.get('https://api.solscan.io/tokens?offset=0&limit=10&sortby=price_change_24h&sorttype=asc');
    let LoserData = data.data.data.tokens;
    if (data.data.data) {
      dispatch(save_loser_data(LoserData));
    }
  }

  //to get holders of chosen token
  const getHolders = async (value) => {
    let url = `https://api.solscan.io/token/holders?token=${value}&offset=0&size=10`;
    let data = await axios.get(url);
    if (data.data.data) {
      let holderData = data.data.data.result;
      dispatch(save_holder_data(holderData));
    }
  }

  //to get chart data of chosen token
  const getChartData = async (value) => {
    let data = await axios.get(`https://api.solscan.io/token/meta?token=${value}`);
    if (data.data && data.data.data) {
      let baseurl = config.serverUrl + '/trade/chart';
      let day = new Date();
      let startTime = day.setDate(day.getDate() - 100);
      startTime = (new Date(startTime)).toISOString();
      let endTime = (new Date()).toISOString();
      axios.post(baseurl, { token: data.data.data.symbol, startTime: startTime, endTime: endTime }).then((response) => {
        if (response.data.data && response.data.data.length > 0) {
          console.log(response.data.data, "chart")
          dispatch(save_chart_data(response.data.data))
        }
        else {
          dispatch(save_chart_data(['']))
        }
      })
    }
    else {
      dispatch(save_chart_data(['']))
    }
    // let marketData = await axios.get(`https://api.solscan.io/amm/market?address=${value}&sort_by=liquidity&sort_type=desc`)
    // console.log(marketData, "marketData")
    // if (marketData.data.data) {
    //   let day = new Date();
    //   let startTime = day.setDate(day.getDate() - 500);
    //   console.log(startTime, "start")
    //   startTime = ((new Date(startTime)).toISOString());
    //   let endTime = ((new Date()).toISOString());
    //   startTime = ((new Date(startTime)).getTime()) / 1000;
    //   endTime = ((new Date(endTime)).getTime()) / 1000;
    //   console.log(startTime, endTime, "why")
    //   let chartData = await axios.get(`https://api.solscan.io/amm/ohlcv?address=${marketData.data.data[0]._id}&type=15m&time_from=${startTime}&time_to=${endTime}`);

    //   if (chartData.data && chartData.data.data && chartData.data.data.items) {
    //     console.log(chartData.data.data.items, "chart Data")
    //     dispatch(save_chart_data(chartData.data.data.items));
    //   }
    //   else {
    //     dispatch(save_chart_data([]));
    //   }
    // }
    // else {
    //   dispatch(save_chart_data([]));
    // }
  }

  //to get promoted token data
  const getPromotedData = async () => {
    let baseURL = 'https://api.solscan.io/tokens?offset=0&limit=10000&sortby=market_cap&sorttype=desc';
    let url = config.serverUrl + "/trade/trade";
    let promotedData = await axios.get(config.serverUrl + "/trade/initData");
    let promotedTokenList = [];
    promotedData = promotedData.data.data;
    let allsDatas = await axios.get(baseURL);
    allsDatas = allsDatas.data.data.tokens;
    for (let i = 0; i < promotedData.length; i++) {
      let promotedTokenAllData = allsDatas.filter(item => item.mintAddress == promotedData[i].coinAddress);
      let promotedTokenData = promotedTokenAllData[0]
      if (promotedTokenData) {
        if (promotedTokenData.mintAddress) {
          promotedTokenData.mintAddress = promotedTokenData.mintAddress;
        }
        if (promotedTokenData.tokenName) {
          promotedTokenData.tokenName = promotedTokenData.tokenName;
        }
        else {
          promotedTokenData.tokenName = '----';
        }
        if (promotedTokenData.icon) {
          promotedTokenData.icon = promotedTokenData.icon;
        }
        else {
          promotedTokenData.icon = '';
        }
        if (promotedTokenData.holder) {
          promotedTokenData.holder = promotedTokenData.holder;
        }
        else {
          promotedTokenData.holder = '----';
        }
        let priceData = await axios.get(`https://api.solscan.io/amm/price?address=${promotedData[i].coinAddress}&source=token`);
        if (priceData.data.data && priceData.data.data.value) {
          promotedTokenData.priceUst = (priceData.data.data.value).toFixed(8);
        }
        else {
          promotedTokenData.priceUst = '----';
        }
        if (promotedTokenData.tokenSymbol) {
          promotedTokenData.tokenSymbol = promotedTokenData.tokenSymbol;
        }
        let otherData = await axios.post(url, { token: promotedTokenData.tokenSymbol });
        let nomicsdata = otherData.data.data.items[0];
        if (nomicsdata) {
          if (nomicsdata['1d'] && nomicsdata['1d'].volume) {
            promotedTokenData.hvolum = nomicsdata['1d'].volume;
          }
          else {
            promotedTokenData.hvolum = '----';
          }
          if (nomicsdata['1d'] && nomicsdata['1d'].price_change_pct) {
            promotedTokenData.changeprice = ((nomicsdata['1d'].price_change_pct) * 100).toFixed(2);
          }
          else {
            promotedTokenData.changeprice = '----'
          }
        }
        promotedTokenList.push(promotedTokenData);

      }
    }
    dispatch(save_promoted_data(promotedTokenList));
  }

  //to get token info
  const getTokenData = async (value) => {
    let token = {};
    let baseURL = 'https://api.solscan.io/tokens?offset=0&limit=10000&sortby=market_cap&sorttype=desc';
    let responseData = await axios.get(baseURL);
    let allData = responseData.data.data.tokens;
    console.log(allData, "gettoken")
    let findDatas = allData.filter(item => item.mintAddress == value);
    let findData = findDatas[0];
    console.log(findData, "find")
    if (findData) {
      token.tokenAddress = findData.mintAddress;
      if (findData.tokenName) {
        token.tokenName = findData.tokenName;
      }
      else {
        token.tokenName = '----';
      }
      if (findData.icon) {
        token.icon = findData.icon;
      }
      else {
        token.icon = '';
      }
      if (findData.holder) {
        token.holder = findData.holder;
      }
      else {
        token.holder = '----';
      }
      let priceData = await axios.get(`https://api.solscan.io/amm/price?address=${value}&source=token`);
      if (priceData.data.data && priceData.data.data.value) {
        token.priceUst = (priceData.data.data.value).toFixed(8);
      }
      else {
        token.priceUst = '----';
      }
      if (findData.twitter) {
        token.twitter = findData.twitter;
      }
      if (findData.website) {
        token.website = findData.website;
      }
      if (findData.tokenSymbol) {
        token.tokenSymbol = findData.tokenSymbol;
      }
      if (findData.supply && findData.supply.uiAmount) {
        token.supply = findData.supply.uiAmount;
      }
      else {
        token.supply = '----';
      }
    }
    let url = config.serverUrl + "/trade/trade";
    await axios.post(url, { token: token.tokenSymbol }).then(async (response) => {
      let data = response.data.data.items[0];
      if (data) {
        if (data['1d'] && data['1d'].volume) {
          token.hvolum = data['1d'].volume;
        }
        else {
          token.hvolum = '----';
        }
        if (data['1d'] && data['1d'].price_change_pct) {
          token.changeprice = ((data['1d'].price_change_pct) * 100).toFixed(2);
        }
        else {
          token.changeprice = '----'
        }
      }
    });
    dispatch(save_token_data(token))
  }

  //to get nft detail data
  const getNFTImage = async (data) => {
    let nftData = [];
    let nftDatakeys = Object.keys(data);
    for (var i = 0; i < nftDatakeys.length; i++) {
      let nftChip = {};
      nftChip.name = data[nftDatakeys[i]][0].name;
      nftChip.count = data[nftDatakeys[i]].length;
      nftChip.address = data[nftDatakeys[i]][0].tokenID;
      let nftUrl = ((data[nftDatakeys[i]][0].uri).split('\u0000'))[0];
      var response = await axios.get(nftUrl);
      if (response.data) {
        nftChip.image = response.data.image;
        nftChip.attr = response.data.attributes;
        nftChip.description = response.data.description;
      }
      else {
        nftChip.attr = '';
        nftChip.image = '';
        nftChip.description = '';
      }
      nftData.push(nftChip);
    }
    dispatch(save_nft_data(nftData));
  }

  //to get nft common data
  const getNFTData = async (value) => {
    let res = {}
    let url = config.serverUrl + '/trade/nftData'
    var response = await axios.post(url, { address: value })
    if (response.data.result === 'success') {
      let data = response.data.data;
      res = data.reduce((acc, curr) => {
        if (!acc[curr.name]) acc[curr.name] = [];
        acc[curr.name].push(curr);
        return acc;
      }, {});
      getNFTImage(res)
    }
  }

  //to choose crrect token for you input 
  const getSearchToken = async (value) => {
    //if current page is Chart page
    var url = window.location.pathname;
    var page = (url.split("/"))[1];
    setSearchKey(value);
    if (page.toLowerCase() === 'chart') {
      if (!value) {
        setSearchData([]);
      }
      else {
        if (value.length > 15) {
          const includeColumns = ['mintAddress', 'tokenName', 'tokenSymbol'];
          const lowercasedValue = value.toLowerCase().trim();
          const filteredData = initData.filter(item => {
            return Object.keys(item).some(key =>
              includeColumns.includes(key) ? item[key].toString().toLowerCase().includes(lowercasedValue) : false
            );
          });
          if (filteredData.length < 50) {
            setSearchData(filteredData);
          }
          else {
            setSearchData(filteredData.slice(0, 20));
          }
        }
        else {
          const includeColumns = ['tokenName', 'tokenSymbol'];
          const lowercasedValue = value.toLowerCase().trim();
          const filteredData = initData.filter(item => {
            return Object.keys(item).some(key =>
              includeColumns.includes(key) ? item[key].toString().toLowerCase().includes(lowercasedValue) : false
            );
          });
          if (filteredData.length < 50) {
            setSearchData(filteredData);
          }
          else {
            setSearchData(filteredData.slice(0, 20));
          }
        }
      }
    }
    else {
      if (page.toLowerCase() === 'nftchecker') {
        getNFTData(value);
      }
      if (page.toLowerCase() === 'addresschecker') {
        dispatch(save_otherAddress_data(value));
      }
    }
  }

  //when the user selects the token
  const chooseToken = (address) => {
    history.push('/Chart?token=' + address);
    getTokenSymbol(address);
    getTokenData(address);
    getChartData(address);
    getHolders(address);
    getPair(address);
    setSearchKey("");
    setSearchData([]);
  }

  //to get token symbol
  const getTokenSymbol = async (value) => {
    let data = await axios.get(`https://api.solscan.io/token/meta?token=${value}`);
    if (data.data && data.data.data) {
      dispatch(save_symbol_data(data.data.data.symbol));
    }
  }

  //when users connect to their wallet
  useEffect(() => {
    if (publicKey) {
      getWalletAddress();
    }
  }, [publicKey])

  useEffect(() => {
    getTokenList();
    getSolvPrice();
    getGainer();
    getLoser();
    getPromotedData();
    var url = window.location.pathname;
    var url_para = window.location.href;
    var url_para_data = new URL(url_para);
    var url_para_address = url_para_data.searchParams.get("token");
    var page = (url.split("/"))[1];
    if (page.toLowerCase() === 'chart') {
      if (url_para_address) {
        getTokenSymbol(url_para_address);
        getTokenData(url_para_address);
        getChartData(url_para_address);
        getPair(url_para_address);
        getHolders(url_para_address);
      }
      else {
        getTokenSymbol('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN');
        getTokenData('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN');
        getChartData('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN');
        getPair('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN');
        getHolders('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN');
      }
    }
  }, []);

  const classes = useStyles();
  return (
    <>
      <AppBar id="mainHeader">
        <Toolbar disableGutters>
          <Container
            maxWidth={false}
            component={Grid}
            classes={{ root: classes.containerRoot }}
          >
            <Grid
              display="flex"
              justifyContent="space-between"
              alignItems="center"
              width="100%"
              id="header"
            >
              <Grid display="flex" container alignItems="center" width="auto">
                <Grid
                  item
                  xs={6}
                  xl={6}
                  sm={12}
                  display="flex"
                  alignItems="center"
                  id="searchBox"
                >
                  <Grid id="inputContainer">
                    <input id="address" placeholder="Search by name, symbol, address." value={searchKey} onChange={(e) => { getSearchToken(e.target.value) }}></input>
                    <SearchIcon id="searchIcon" />
                  </Grid>
                  <ul id="searchList">
                    {searchData ? searchData.map((item, key) => (
                      <li key={key} id="eachToken" onClick={() => chooseToken(item.mintAddress)}>
                        <Grid container>
                          <Grid item id="tokenIcon">
                            <img id="tokenSearchIcon" src={item.icon}></img>
                          </Grid>
                          <Grid item>
                            <Grid id="tokenName" item xs={12} xl={12}>
                              {item.tokenName}
                            </Grid>
                            <Grid id="tokenSymbol" item xs={12} xl={12}>
                              {item.tokenSymbol}
                            </Grid>
                          </Grid>
                        </Grid>
                      </li>
                    )) : ''}
                  </ul>
                </Grid>
                <Grid item xs={2} xl={3} sm={6} className="buttonGroup" >
                  <Button id="usdButton1" onClick={() => getSolvPrice}>USD</Button>
                  <Button id="usdButton2"><img id="usd" src={solviewPng}></img>&nbsp;${(solvPrice).toFixed(4)}</Button>
                </Grid>
                <Grid item xs={4} xl={3} sm={6} className="buttonGroup1">
                  <Button ><img src={settingPng}></img></Button>
                  <WalletMultiButton id="walletConnect" />
                </Grid>
              </Grid>
            </Grid>
          </Container>
        </Toolbar>
      </AppBar>
    </>
  );
}
