import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import FormControl from "@material-ui/core/FormControl";
import InputAdornment from "@material-ui/core/InputAdornment";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import Search from "@material-ui/icons/Search";
import "layouts/NftChecker.css"
import Sidebar from "components/Sidebar/Sidebar.js";
import NavbarDropdown from "components/Dropdowns/NavbarDropdown.js";
import routes from "routes.js";
import componentStyles from "assets/theme/layouts/admin.js";
import Grid from "@material-ui/core/Grid";
import { useConnection, useWallet } from '@solana/wallet-adapter-react';
import "assets/css/layouts/StakingLayout.scss"
import axios from "axios";
import { useSelector } from "react-redux";


const useStyles = makeStyles(componentStyles);
const StakingLayout = () => {
    const classes = useStyles();
    const location = useLocation();
    const { connection } = useConnection();
    const { publicKey, sendTransaction, keypair } = useWallet();
    const wallet = useWallet();
    const [solvBalance, setSolvBalance] = useState(0);
    const address = useSelector((store) => store.provider.address);
    // const address = '9BqViyehpiQjn1ZHDcJt321JRBVxJj9ouCKjfLmMGrtX';
    const [apyPercent, setApyPercent] = useState(0);
    const [lockPeriod, setLockPeriod] = useState(7);
    const getTokenList = async () => {
        const TokenListDatas = await axios.get('https://api.solscan.io/account/tokens?address=' + address + '&price=1');
        if (TokenListDatas.data.succcess === true) {
            const TokenListData = TokenListDatas.data.data;
            if (TokenListData) {
                let findData = TokenListData.filter(item => item.tokenAddress == "7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN");
                console.log(findData, "data")
                if (findData) {
                    setSolvBalance(findData[0].tokenAmount.uiAmount);
                }
            }
        }
    }

    useEffect(() => {
        getTokenList();
    }, [address])
    useEffect(() => {
        document.documentElement.scrollTop = 0;
        document.scrollingElement.scrollTop = 0;
    }, [location]);

    return (
        <>
            <>
                <Sidebar
                    routes={routes}
                    logo={{
                        innerLink: "/Chart",
                        imgSrc: require("../assets/img/brand/solview.png").default,
                        imgAlt: "...",
                    }}
                    dropdown={<NavbarDropdown />}
                    input={
                        <FormControl variant="outlined" fullWidth >
                            <InputLabel htmlFor="outlined-adornment-search-responsive">
                                Address
                            </InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-search-responsive"
                                type="text"
                                endAdornment={
                                    <InputAdornment position="end">
                                        <Box
                                            component={Search}
                                            width="1.25rem!important"
                                            height="1.25rem!important"
                                        />
                                    </InputAdornment>
                                }
                                labelWidth={70}
                            />
                        </FormControl>
                    }
                />
                <Box position="relative" className={classes.mainContent}>
                    <Box id="staking">
                        <Box id="stakingModal">
                            <Box id="stkTitle">Stack for IDO Participation</Box>
                            <Box id='stkDetBox'><input id="stkDetail" placeholder="Enter Details"></input></Box>
                            <Box id='stkBtnGrp'>
                                <Button id="stkDayBtn" onClick={() => { setApyPercent(0); setLockPeriod(7); }}>7 Days</Button>
                                <Button id="stkDayBtn" onClick={() => { setApyPercent(5); setLockPeriod(14); }}>14 Days</Button>
                                <Button id="stkDayBtn" onClick={() => { setApyPercent(10); setLockPeriod(30); }}>30 Days</Button>
                            </Box>
                            <Box id="stkBrBox">
                                <Box id="stkBorder"></Box>
                            </Box>
                            <Box id="stkApy">
                                <Button id="apyBtn">{apyPercent}% APY*</Button>
                            </Box>
                            <Box id="stkOption"><span id="opTitle">Lock period</span><span id="opVal">{lockPeriod} Days</span></Box>
                            <Box id="stkOption"><span id="opTitle">Extends Lock On Registeration</span><span id="opVal">Yes</span></Box>
                            <Box id="stkOption"><span id="opTitle">Early Unstake Fee</span><span id="opVal">25%</span></Box>
                            <Box id="stkOption"><span id="opTitle">Status</span><span id="opVal">Unlocked</span></Box>
                            <Grid containter id="stkMain">
                                <Grid item id="stkBalance" container xs={5} xl={5}>
                                    <Grid item id="balance" xs={12} xl={12}>Balance: {solvBalance}SOLV</Grid>
                                    <Grid item id="stkAmtBox" xs={12} xl={12}>
                                        <input id="stkAmount"></input>
                                    </Grid>
                                </Grid>
                                <Grid item id="stkMax" xs={3} xl={3}>
                                    <button id="maxBtn">Max</button>
                                    <span id="solvName">SOLV</span>
                                </Grid>
                                <Grid item id="stkApprove" xs={4} xl={4}>
                                    <Button id="approveBtn">Approve</Button>
                                </Grid>
                            </Grid>
                            <Grid containter id="stkMain">
                                <Grid item id="stkBalance" container xs={5} xl={5}>
                                    <Grid item id="balance" xs={12} xl={12}>Staked: 0SOLV</Grid>
                                    <Grid item id="stkAmtBox" xs={12} xl={12}>
                                        <input id="stkAmount"></input>
                                    </Grid>
                                </Grid>
                                <Grid item id="stkMax" xs={3} xl={3}>
                                    <button id="maxBtn">Max</button>
                                    <span id="solvName">SOLV</span>
                                </Grid>
                                <Grid item id="stkApprove" xs={4} xl={4}>
                                    <Button id="approveBtn">Unstake</Button>
                                </Grid>
                            </Grid>

                            <Box id="footer"><span>APY is Dynamic*</span></Box>
                        </Box>
                    </Box>
                    <Container
                        maxWidth={false}
                        component={Box}
                        classes={{ root: classes.containerRoot }}
                    >
                    </Container>
                </Box>
            </>
        </>
    );
};

export default StakingLayout;
