import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import FormControl from "@material-ui/core/FormControl";
import InputAdornment from "@material-ui/core/InputAdornment";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import Search from "@material-ui/icons/Search";
import "layouts/NftChecker.css"
import Sidebar from "components/Sidebar/Sidebar.js";
import NavbarDropdown from "components/Dropdowns/NavbarDropdown.js";
import routes from "routes.js";
import componentStyles from "assets/theme/layouts/admin.js";
import NftChecker from "views/main/NFTChecker";
import { useDispatch } from "react-redux";
import { save_nft_data } from "redux/actions/provider";
import { change_header } from "redux/actions/provider";
import config from "config/index.js"
import axios from "axios";

const useStyles = makeStyles(componentStyles);
const Admin = () => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const location = useLocation();
  const [address, addressSet] = useState('');

  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
  }, [location]);

  const getNFTImage = async (data) => {
    let nftData = [];
    let nftDatakeys = Object.keys(data);
    for (var i = 0; i < nftDatakeys.length; i++) {
      let nftChip = {};
      nftChip.name = data[nftDatakeys[i]][0].name;
      nftChip.count = data[nftDatakeys[i]].length;
      nftChip.address = data[nftDatakeys[i]][0].tokenID;
      let nftUrl = ((data[nftDatakeys[i]][0].uri).split('\u0000'))[0];
      var response = await axios.get(nftUrl);
      if (response.data) {
        nftChip.image = response.data.image;
        nftChip.attr = response.data.attributes;
        nftChip.description = response.data.description;
      }
      else {
        nftChip.attr = '';
        nftChip.image = '';
        nftChip.description = '';

      }
      nftData.push(nftChip);
      console.log(nftData, "nftData")
    }
    dispatch(save_nft_data(nftData));
  }

  const tokenSearch = async () => {
    dispatch(change_header('false'))
    let res = {}
    let url = config.serverUrl + '/trade/nftData'
    var response = await axios.post(url, { address: address })
    if (response.data.result === 'success') {
      let data = response.data.data;
      res = data.reduce((acc, curr) => {
        if (!acc[curr.name]) acc[curr.name] = [];
        acc[curr.name].push(curr);
        return acc;
      }, {});
      getNFTImage(res)
    }
  }

  //When the user inputs token address(mobile)
  const changeAddress = (value) => {
    addressSet(value);
  }

  return (
    <>
      <>
        <Sidebar
          routes={routes}
          logo={{
            innerLink: "/Chart",
            imgSrc: require("../assets/img/brand/solview.png").default,
            imgAlt: "...",
          }}
          dropdown={<NavbarDropdown />}
          input={
            <FormControl variant="outlined" fullWidth >
              <InputLabel htmlFor="outlined-adornment-search-responsive">
                Address
              </InputLabel>
              <OutlinedInput
                onChange={(event) => changeAddress(event.target.value)}
                id="outlined-adornment-search-responsive"
                type="text"
                endAdornment={
                  <InputAdornment position="end" onClick={() => tokenSearch()}>
                    <Box
                      component={Search}
                      width="1.25rem!important"
                      height="1.25rem!important"
                    />
                  </InputAdornment>
                }
                labelWidth={70}
              />
            </FormControl>
          }
        />
        <Box position="relative" className={classes.mainContent}>
          <NftChecker />
          <Container
            maxWidth={false}
            component={Box}
            classes={{ root: classes.containerRoot }}
          >
          </Container>
        </Box>
      </>
    </>
  );
};

export default Admin;
